//漢堡選單
$('.showmenu').click(function (e) {
    e.preventDefault();
    // $('body').toggleClass('menu-show');
    $('.dropdown').toggleClass('active');
})


// QA選單折疊
$(".question h3").click(function () {
    $(this).toggleClass('active');
    $(this).parent().find('p').slideToggle();
    $(this).parent().siblings().find('p').slideUp();
    $(this).parent().siblings().find('h3').removeClass('active');

});

//top & Down
$(document).scroll(function () {
    let y = $(this).scrollTop();
    // console.log(y);
    if (y > 1200) {
        $('.goTop').fadeIn(600);
    } else {
        $('.goTop').fadeOut(600);
    }
});
$(document).scroll(function () {
    let y = $(this).scrollTop();
    // console.log(y);
    if (y > 600) {
        $('.goDown').fadeIn(600);
    } else {
        $('.goDown').fadeOut(600);
    }
});

$(".goTop a").click(function (event) {
    event.preventDefault();//取消#錨點

    $('html,body').animate({
        scrollTop: 0
    }, 1200);

});

$(".goDown a").click(function (event) {
    event.preventDefault();//取消#錨點

    $('html,body').animate({
        scrollTop: $(document).height() - $(window).height() - 190
    }, 1200);

}); 

// 經典系列的分頁效果
$('#classicList li a').click(function (e) {
    e.preventDefault();
    //點擊連結時增加active表示當前頁面
    $(this).parent().toggleClass('active');
    $(this).parent().siblings().removeClass('active');
    //設變數取得a href的值
    var tab_id = $(this).attr('href');
    console.log(tab_id);
    $(tab_id).addClass('active');
    $(tab_id).siblings('.active').removeClass('active');
});


